<template>
    <div class="chart">
        <div ref="sunchart" class="echarts" style="width:800px;height:500px"></div>
    </div>
</template>

<script>
    import { animate } from "@/function/animate.js";
    import * as echarts from "echarts/core";
    import { TitleComponent, ToolboxComponent, TooltipComponent, GridComponent, LegendComponent } from "echarts/components";
    import { SunburstChart } from "echarts/charts";
    import { SVGRenderer } from "echarts/renderers";
    export default {
        props: ["Dataset", "Season"],
        async mounted() {
            echarts.use([TitleComponent, ToolboxComponent, TooltipComponent, GridComponent, LegendComponent, SunburstChart, SVGRenderer]);

            this.myChart = echarts.init(this.$refs.sunchart);
            let data = await fetch("./summer中国奖牌.json", { cache: "force-cache" }).then((res) => res.json());

            data = Object.values(data);
            const Year = [...new Set(data.map((i) => i.Year))].sort((a, b) => a - b);

            this.option.series = {
                type: "sunburst",
                emphasis: {
                    focus: "ancestor",
                },
                position: "center",

                data: Year.map((year, index) => {
                    console.log(index % 5);
                    return {
                        name: year + "",
                        children: data
                            .filter((i) => year == i.Year)
                            .map((i) => {
                                let name = "";
                                switch (i.Medal) {
                                    case "Gold":
                                        name = "金牌";
                                        break;
                                    case "Silver":
                                        name = "银牌";
                                        break;
                                    case "Bronze":
                                        name = "铜牌";
                                        break;
                                }
                                return {
                                    name: name + "-" + i.count,
                                    value: i.count,

                                    itemStyle: { color: i.Medal == "Bronze" ? "#b87333" : i.Medal },
                                    label: { color: i.Medal == "Bronze" ? "#b87333" : i.Medal },
                                };
                            })
                            .sort((a, b) => a.index - b.index),
                        label: { color: "#fff" },
                    };
                }),
                radius: [100, "90%"],
                itemStyle: {
                    borderRadius: 15,
                    borderWidth: 5,
                },
                label: { normal: { textStyle: { fontSize: 16 } } },
                sort: null,
                levels: [
                    {},
                    {},
                    {
                        r0: "67%",
                        r: "74%",
                        label: {
                            position: "outside",
                            padding: 3,
                            silent: false,
                        },
                        itemStyle: {
                            borderWidth: 3,
                            color: "#000",
                        },
                    },
                ],
            };
            console.log(this.option);
            this.myChart.setOption(this.option);
            this.myChart.on("rendered", () => {
                if (!this.rendered) {
                    let odiv = document.createElementNS("http://www.w3.org/2000/svg", "text");
                    this.$refs.sunchart.querySelector("svg").appendChild(odiv);
                    odiv.setAttribute("xml:space", "preserve");
                    odiv.setAttribute("fill", "#464646");
                    odiv.setAttribute("fill-opacity", "1");
                    odiv.setAttribute("stroke", "none");
                    odiv.setAttribute("transform", "matrix(1,0,0,1,400,250)");
                    odiv.setAttribute("dominant-baseline", "central");
                    odiv.setAttribute("text-anchor", "middle");
                    odiv.setAttribute("x", "0");
                    odiv.setAttribute("y", "0");
                    odiv.setAttribute("style", 'font: bold 18px "Microsoft YaHei";');
                    odiv.innerHTML = "中国奖牌图";
                    this.rendered = true;
                }
            });

            this.myChart.on("click", (event) => {
                console.log(event);
                odiv.innerHTML = event.name ? event.name + "年 中国奖牌数" : "中国奖牌图";
                animate(odiv, "fadeIn");
                console.log(odiv);
            });
            console.log(this.myChart);
        },
        data() {
            return {
                myChart: {},
                rendered: false,
                option: {
                    label: {
                        color: "#000",
                    },
                    tooltip: {
                        trigger: "axis",
                    },
                    legend: {},
                    grid: {
                        left: "3%",
                        right: "4%",
                        bottom: "3%",
                        containLabel: true,
                    },

                    series: [],
                },
            };
        },
    };
</script>
<style lang="less" scoped></style>
